import React, {useEffect, useState} from 'react';
import {Checkbox, Collapse, Input, InputNumber, Radio, Select, Tooltip} from "antd";
import { InfoCircleFilled } from "@ant-design/icons"
import alpha from "../img/icons/alpha.png"
import ClearAllBtn from "../components/ClearAllBtn";
import PlusCircleTwoTone from "@ant-design/icons/lib/icons/PlusCircleTwoTone";
import InputYear from "../components/requests/InputYear";

const Calc = () => {
    const { Option } = Select;
    const { Panel } = Collapse;
    const [price, setPrice] = useState('');
    const [initialFee, setInitialFee] = useState('');
    const [sumPart, setSumPart] = useState('');
    const [propertyType, setPropertyType] = useState('');
    const [checkedList, setCheckedList] = useState([]);
    const [jobList, setJobList] = useState([]);
    const [calculationType, setCalculationType] = useState(1);

    function onChangeInitialFee(value) {
        setInitialFee(value);
    }

    function onPriceChange(value) {
        setPrice(value);
    }

    useEffect(() => {
        if (price && initialFee) {
            setSumPart((initialFee/price*100).toFixed (2) + '%');
        } else {
            setSumPart('');
        }
    }, [price, initialFee])

    function onChangeCreditProps(e) {
        if (e.target.checked) {
            setCheckedList([e.target.value]);
        } else {
            setCheckedList([]);
        }
    }
    function onChangeJobProps(e) {
        if (e.target.checked) {
            setJobList([e.target.value]);
        } else {
            setJobList([]);
        }
    }

    const clearALlBtn = () => (
        <ClearAllBtn onClick={ event => {
            console.log('tt');
            event.stopPropagation();
        }}/>
        );

    const onChangeCalcType = (e) => {
        setCalculationType(e.target.value);
        setPrice(undefined);
    }

    return (
        <div className="container">
            <div className="calc-wrap">
            <div className="calc-header">
                <div className="calc-header__col">
                    <div className="left-red-Line">  </div>
                    <div className="partner-short">
                        <div className="partner-short__icon">
                            <img src={alpha} alt=""/>
                        </div>
                        <div className="partner-short__text">
                            <div className="partner-short__name">  - </div>
                            <div className="partner-short__desc">         </div>
                        </div>
                    </div>
                </div>
                <div className="calc-header__col">
                    <div className="calc-header__title3">
                                 .
                    </div>
                    { (propertyType && initialFee && price)
                        ?
                        <div className="calc-header__text">
                                 ,     .
                        </div>
                        :
                        <div className="calc-header__text">
                                   ,   :
                            { !propertyType && <span>   ,</span> }
                            { !initialFee && <span>  , </span> }
                            { !price && <span>  .</span> }
                        </div>
                    }
                </div>
            </div>
            <div className="calc-body">
                <div className="form-field">
                    <div className="form-field__row form-field__row--flex label-on-top">
                        <div className="form-field__col4">
                            <label> </label>
                            <Select defaultValue="mortgage" dropdownClassName="with-radio">
                                <Option value="refinancing">
                                    <span className="checkmark"></span>
                                    <span></span>
                                </Option>
                                <Option value="mortgage">
                                    <span className="checkmark"></span>
                                    <span></span>
                                </Option>
                            </Select>
                        </div>
                        <div className="form-field__col4">
                            <label> </label>
                            <Select dropdownClassName="with-radio" listHeight={290}>
                                <Option value="developer">
                                    <div className="with-desc">
                                        <div className="row">
                                            <span className="checkmark"></span>
                                            <span></span>
                                        </div>
                                        <div className="desc"> </div>
                                    </div>
                                </Option>
                                <Option value="individual">
                                    <div className="with-desc">
                                        <div className="row">
                                            <span className="checkmark"></span>
                                            <span></span>
                                        </div>
                                        <div className="desc"> </div>
                                    </div>
                                </Option>
                                <Option value="entity">
                                    <div className="with-desc">
                                        <div className="row">
                                            <span className="checkmark"></span>
                                            <span></span>
                                        </div>
                                        <div className="desc"> </div>
                                    </div>
                                </Option>
                                <Option value="assignmentIndividual">
                                    <div className="with-desc">
                                        <div className="row">
                                            <span className="checkmark"></span>
                                            <span>  </span>
                                        </div>
                                        <div className="desc"> </div>
                                    </div>
                                </Option>
                                <Option value="assignmentEntity">
                                    <div className="with-desc">
                                        <div className="row">
                                            <span className="checkmark"></span>
                                            <span>  </span>
                                        </div>
                                        <div className="desc"> </div>
                                    </div>
                                </Option>
                            </Select>
                        </div>
                        <div className="form-field__col4">
                            <label> </label>
                            <Select dropdownClassName="with-radio" onChange={setPropertyType}>
                                <Option value="house">
                                    <span className="checkmark"></span>
                                    <span>  </span>
                                </Option>
                                <Option value="flat">
                                    <span className="checkmark"></span>
                                    <span></span>
                                </Option>
                                <Option value="room">
                                    <span className="checkmark"></span>
                                    <span>  </span>
                                </Option>
                            </Select>
                        </div>
                        <div className="form-field__col4">
                            <label></label>
                            <Select dropdownClassName="with-radio">
                                <Option value="moscow">
                                    <span className="checkmark"></span>
                                    <span>Moscow</span>
                                </Option>
                                <Option value="piter">
                                    <span className="checkmark"></span>
                                    <span>Piter</span>
                                </Option>
                            </Select>
                        </div>
                    </div>
                    <div className="form-field__row">
                        <div className="form-field__title"> </div>
                        <div>
                            <Radio.Group value={calculationType} onChange={onChangeCalcType}>
                                <Radio value={1}> </Radio>
                                <Radio value={2}> </Radio>
                                <Radio value={3}></Radio>
                            </Radio.Group>
                        </div>
                    </div>
                    <div className="form-field__row form-field__row--flex label-on-top">
                        <div className="form-field__col4 input-year">
                            <InputYear/>
                        </div>
                        <div className="form-field__col4">
                            {{
                                1: <><label> , </label><InputNumber value={price} min={0} max={10000000} step={100000} onChange={onPriceChange}/></>,
                                2: <><label>  , </label><InputNumber min={0} max={10000000} step={10000}/></>,
                                3: <><label>, </label><InputNumber min={0} max={10000000} step={10000}/></>
                            }[calculationType]}
                        </div>
                        <div className="form-field__col4 input-part">
                            <label> , </label>
                            <InputNumber min={0} max={10000000} step={100000} addonAfter={sumPart} onChange={onChangeInitialFee}/>
                        </div>
                    </div>
                    <div className="form-field__row">
                        <Checkbox.Group value={checkedList}>
                            <Checkbox onChange={onChangeCreditProps} value={1}> </Checkbox>
                            <Checkbox onChange={onChangeCreditProps} value={2}>
                                 
                                <Tooltip placement="rightTop" title="  ,    01.01.2018  31.12.2022  ">
                                    <InfoCircleFilled style={{ marginLeft: '1rem', color: '#8D8D9B' }}/>
                                </Tooltip>
                            </Checkbox>
                            <Checkbox onChange={onChangeCreditProps} value={3}></Checkbox>
                        </Checkbox.Group>
                        <Checkbox> </Checkbox>
                    </div>
                    <div className="form-field__row form-field__row--flex">
                        <div className="form-field__col3">
                            <Collapse expandIconPosition={"right"}>
                                <Panel header="" key="1" extra={clearALlBtn()}>
                                    <div className="form-field__row label-on-top">
                                        <div className="form-field__col">
                                            <label>, </label>
                                            <Input/>
                                        </div>
                                    </div>
                                    <div className="form-field__row label-on-top">
                                        <div className="form-field__col">
                                            <label> </label>
                                            <Select dropdownClassName="with-radio">
                                                <Option value="married">
                                                    <span className="checkmark"></span>
                                                    <span> </span>
                                                </Option>
                                                <Option value="single">
                                                    <span className="checkmark"></span>
                                                    <span></span>
                                                </Option>
                                            </Select>
                                        </div>
                                    </div>
                                    <div className="form-field__row">
                                        <div className="form-field__title"></div>
                                        <div>
                                            <Radio.Group>
                                                <Radio value={1}></Radio>
                                                <Radio value={2}></Radio>
                                            </Radio.Group>
                                        </div>
                                    </div>
                                    <div className="form-field__row">
                                        <div className="form-field__title"></div>
                                        <div>
                                            <Radio.Group>
                                                <Radio value={1}></Radio>
                                                <Radio value={2}></Radio>
                                            </Radio.Group>
                                        </div>
                                    </div>
                                </Panel>
                            </Collapse>
                        </div>
                        <div className="form-field__col3">
                            <Collapse expandIconPosition={"right"}>
                                <Panel header="" key="2" extra={clearALlBtn()}>
                                    <div className="form-field__row">
                                        <div className="form-field__title"></div>
                                        <div>
                                            <Radio.Group>
                                                <Radio value={1}></Radio>
                                                <Radio value={2}></Radio>
                                                <Radio value={3}></Radio>
                                            </Radio.Group>
                                        </div>
                                    </div>
                                    <div className="form-field__row gray-line">
                                        <Checkbox.Group value={jobList}>
                                            <div className="checkRow">
                                                <Checkbox onChange={onChangeJobProps} value={1}>  </Checkbox>
                                            </div>
                                            <div className="checkRow">
                                                <Checkbox onChange={onChangeJobProps} value={2}>  </Checkbox>
                                            </div>
                                        </Checkbox.Group>
                                    </div>
                                    <div className="form-field__row label-on-top">
                                        <div className="form-field__col tooltip-top">
                                            <label>  </label>
                                            <Input/>
                                            <Tooltip placement="rightTop" title=" ,     ">
                                                <InfoCircleFilled style={{ marginLeft: '1rem', color: '#8D8D9B' }}/>
                                            </Tooltip>
                                        </div>
                                    </div>
                                    <div className="form-field__row label-on-top">
                                        <div className="form-field__col">
                                            <label>   </label>
                                            <Input/>
                                        </div>
                                    </div>
                                    <div className="form-field__row label-on-top">
                                        <div className="form-field__col">
                                            <label> </label>
                                            <Select dropdownClassName="with-radio" listHeight={290}>
                                                <Option value={1}>
                                                    <span className="checkmark"></span>
                                                    <span>  </span>
                                                </Option>
                                                <Option value={2}>
                                                    <span className="checkmark"></span>
                                                    <span>2-</span>
                                                </Option>
                                                <Option value={3}>
                                                    <span className="checkmark"></span>
                                                    <span>    </span>
                                                </Option>
                                                <Option value={4}>
                                                    <span className="checkmark"></span>
                                                    <span> </span>
                                                </Option>
                                                <Option value={5}>
                                                    <div className="with-desc">
                                                        <div className="row">
                                                            <span className="checkmark"></span>
                                                            <span> </span>
                                                        </div>
                                                        <div className="desc">   </div>
                                                    </div>
                                                </Option>
                                            </Select>
                                        </div>
                                    </div>
                                    <div className="form-field__row check-right-tooltip">
                                        <div className="check-right-tooltip__left">
                                            <Checkbox>    </Checkbox>
                                        </div>
                                        <div className="check-right-tooltip__right">
                                            <Tooltip placement="rightTop" title=" ,      ">
                                                <InfoCircleFilled style={{ marginLeft: '1rem', color: '#8D8D9B' }}/>
                                            </Tooltip>
                                        </div>
                                    </div>
                                </Panel>
                            </Collapse>
                        </div>
                        <div className="form-field__col3">
                            <Collapse expandIconPosition={"right"}>
                                <Panel header="" key="3" extra={clearALlBtn()}>
                                    <div className="form-field__row">
                                        <div className="btn-add">
                                            <div className="btn-add__icon"><PlusCircleTwoTone /></div>
                                            <div> </div>
                                        </div>
                                    </div>
                                </Panel>
                            </Collapse>
                        </div>
                    </div>
                    <div className="form-field__row ta-right">
                        <div className="btn btn-red"> </div>
                    </div>
                </div>
            </div>
        </div>
        </div>
    );
};

export default Calc;